import React from 'react';
import {Panel, Div, Button, Cell, Avatar, ModalRoot, ModalPage, ModalPageHeader, HeaderButton, IS_PLATFORM_IOS, IS_PLATFORM_ANDROID, ModalCard, FixedLayout} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import VKStories from "vk-stories";
import connect from "@vkontakte/vkui-connect-promise";
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Done from '@vkontakte/icons/dist/24/done';
import img from '../img/nn.jpg';
VKStories.init(connect);




//компонент модального окна
class Modal extends React.Component {
constructor(props) {
super(props);
}





GenerateStory = () => {
  connect.send("VKWebAppShowStoryBox",
  {
      "background_type": "image",
      "url": "https://www.publicdomainpictures.net/pictures/90000/nahled/clip-art-sun-rays-3.jpg",
      "sticker_container": [
        {
          "sticker_type": "renderable",
          "sticker": {
            "content_type": "image",
            "url": "https://c.pxhere.com/images/02/59/5050e95c6bc06b27ed49b1b7d2f4-1434663.jpg!d",
            "clickable_zones": [
              {
                "action_type": "hashtag",
                "action": {
                  "hashtag": "#cancel"
                },
                "clickable_area": [
                  {
                    "x": 17,
                    "y": 110
                  },
                  {
                    "x": 97,
                    "y": 110
                  },
                  {
                    "x": 97,
                    "y": 132
                  },
                  {
                    "x": 17,
                    "y": 132
                  }
                ]
              },
              {
                "action_type": "hashtag",
                "action": {
                  "hashtag": "#okey"
                },
                "clickable_area": [
                  {
                    "x": 109,
                    "y": 109
                  },
                  {
                    "x": 188,
                    "y": 109
                  },
                  {
                    "x": 188,
                    "y": 133
                  },
                  {
                    "x": 109,
                    "y": 133
                  }
                ]
              }
            ],
            "transform": {
              "relation_width": 0.55,
              "gravity": "center_bottom",
              "translation_y": -0.25
            }
          }
        }
      ]
    })
}







render() {
return(
<ModalRoot activeModal={'story'}>
<ModalCard
id={'story'}
onClose={() => this.setActiveModal(null)}
icon={<Avatar src={this.props.selectedPlace.img_story} size={72} />}
title="Поделиться местом с друзьми"
caption="Пусть друзья узнают, куда вы добрались!!!"
style={{}}
onClose={()=>{this.props.cancelHistory()}}
actions={[{
title: 'Поделиться',
type: 'primary',
action: () => {
this.props.sendHistory();
this.GenerateStory();
}
}
]}
/>
</ModalRoot>

);
}

}

export default Modal;
